@import '../variables';

.sort {
  position: relative;
  &__label {
    display: flex;
    align-items: center;
   margin-right: 10px;
   @media(max-width:1170px){
    display: flex;
     align-items: center;
     justify-content: center;
     margin-top: 20px;
   }


    svg {
      margin-right: 8px;
    }

    b {
      margin-right: 8px;
      @media(max-width:1300px){
       display: none;
       }
       @media(max-width:1170px){
       display: flex;
        align-items: center;
      }
      
    }

    span {
      color: $orange;
      border-bottom: 1px dashed $orange;
      cursor: pointer;
    }
  }

  &__popup {
    position: absolute;
    right: 0;
    margin-top: 15px;
    background: #ffffff;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.09);
    border-radius: 10px;
    overflow: hidden;
    padding: 10px 0;
    width: 200px;
    
   
    ul {
      overflow: hidden;
      li {
        padding: 12px 20px;
        cursor: pointer;

        &.active,
        &:hover {
          background: rgba(254, 95, 30, 0.05);
        }

        &.active {
          font-weight: bold;
          color: $orange;
        }
      }
    }
  }
}
